import React, { useState } from 'react';
import { createAuction } from '../utils/auction';

const CreateAuction = ({ account }) => {
    const [startingPrice, setStartingPrice] = useState('');
    const [endTime, setEndTime] = useState('');

    const handleCreateAuction = async () => {
        try {
            const result = await createAuction(
                startingPrice,
                Math.floor(Date.now() / 1000) + parseInt(endTime),
                account
            );
            alert('Auction created successfully!');
            console.log(result);
        } catch (error) {
            alert('Error creating auction. Check the console for details.');
            console.error(error);
        }
    };

    return (
        <div>
            <h2>Create Auction</h2>
            <input
                type="number"
                placeholder="Starting Price (in Wei)"
                value={startingPrice}
                onChange={(e) => setStartingPrice(e.target.value)}
            />
            <input
                type="number"
                placeholder="End Time (seconds from now)"
                value={endTime}
                onChange={(e) => setEndTime(e.target.value)}
            />
            <button onClick={handleCreateAuction}>Create Auction</button>
        </div>
    );
};

export default CreateAuction;