import React, { useEffect, useState } from 'react';
import { getAuctionDetails } from '../utils/auction';

const DisplayAuctions = () => {
    const [auctionDetails, setAuctionDetails] = useState(null);

    useEffect(() => {
        const fetchDetails = async () => {
            try {
                const details = await getAuctionDetails();
                setAuctionDetails(details);
            } catch (error) {
                console.error('Error fetching auction details:', error);
            }
        };

        fetchDetails();
    }, []);

    return (
        <div>
            <h2>Ongoing Auction</h2>
            {auctionDetails ? (
                <div>
                    <p>Highest Bidder: {auctionDetails.highestBidder}</p>
                    <p>Highest Bid: {auctionDetails.highestBid} Wei</p>
                    <p>Starting Price: {auctionDetails.startingPrice} Wei</p>
                    <p>Current Price: {auctionDetails.currentPrice} Wei</p>
                    <p>End Time: {new Date(auctionDetails.endTime * 1000).toLocaleString()}</p>
                </div>
            ) : (
                <p>Loading auction details...</p>
            )}
        </div>
    );
};

export default DisplayAuctions;